@font-face {
    font-family: "Circe";
    src: url("roboto/Roboto-Regular.otf") format("opentype"),
    url('roboto/Roboto-Regular.woff2') format('woff2'),
    url('roboto/Roboto-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: "Circe";
    src: url("roboto/Roboto-Medium.ttf");
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: "Circe";
    src: url("roboto/Roboto-Bold.otf") format("opentype"),
    url('roboto/Roboto-Bold.woff2') format('woff2'),
    url('roboto/Roboto-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: "Nizhegorodskiy";
    src: url("Nizhegorodsky/Nizhegorodsky.otf") format("opentype");
    font-weight: normal;
    font-style: normal;
}
